/******************************************
 *         components/product-sort
 =========================================*/
.product-nav {
  position: relative;
  margin-bottom: 1rem;
  overflow: hidden;
  background-color: #F8F8F8;
  white-space: nowrap;
  color: #666;
  font-size: 0;
  border: 2px solid #CCC; }

.product-nav--block {
  padding: 0.75rem;
  line-height: 1;
  display: inline-block;
  font-size: 1rem;
  border-left: 2px solid #CCC; }
  @media only screen and (max-width: 600px) {
    .product-nav--block {
      display: block;
      border-left: none;
      border-top: 2px solid #CCC;
      text-align: center; }
      .product-nav--block:first-child {
        border-top: none; } }
  .product-nav--block:first-child {
    border-left: none; }
  .product-nav--block input {
    margin: 0; }

.product-nav--block--basket {
  position: absolute;
  right: 0; }

.sort-up, .sort-down {
  display: inline-block; }

.sort-up:before,
.sort-down:before {
  margin-right: 0.25rem;
  line-height: 80%;
  color: inherit;
  font-family: 'icon';
  font-size: 50%;
  content: "e"; }

.sort-up:before {
  display: inline-block;
  transform: rotate(180deg); }
